import { connect } from 'react-redux';
import SoundConfig from '../components/SoundConfig';
import actions from '../modules/rockhopper/actions';
import { selectSoundConfig } from '../modules/rockhopper/selectors';


export default connect( 
(state, ownProps) => {
  return {
    soundConfig: selectSoundConfig(state),
  }
}, 
(dispatch, ownProps) => ({
  onSetVolume: (volume) => {
    dispatch( actions.updateHelpers.sound.volume(volume) );
    dispatch( actions.put.saveClientConfig({ data: [ 'soundConfig', volume ] }) );
  },
  onSetSoundConfig: (newConfig) => {
    dispatch( actions.updateHelpers.soundConfig(newConfig) );
    dispatch( actions.put.saveClientConfig({ data: [ 'soundConfig', newConfig ] }) );
  }
}))(SoundConfig);
