import React from 'react';

import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';

import { makeStyles } from '@material-ui/styles';
import useTranslation from '../hooks/useTranslation';

const useStyles = makeStyles((theme) => ({
  select: {
    textTransform: "capitalize"
  }
}));

const JogModeSelect = (props) => {
  const {
    jogMode,
    onSetJogMode
  } = props;

  const { t } = useTranslation();
  const classes = useStyles();

  const handleChangeJogMode = (event) => {
    if(onSetJogMode){
      onSetJogMode(event.target.value)
    }
  };

  return <>
    <Select labelId="jog-mode-select-label" id="jog-mode-select" value={jogMode} onChange={handleChangeJogMode} className={classes.select}>
      {/* <MenuItem value={"AUTO"}>{t("auto")}</MenuItem> */}
      <MenuItem value={"JOINT"}>{t("joint")}</MenuItem>
      <MenuItem value={"AXIS"}>{t("axis")}</MenuItem>
    </Select>
  </>
};
export default JogModeSelect;
