import { connect } from 'react-redux';
import actions from '../modules/rockhopper/actions';
import JogModeSelect from '../components/JogModeSelect';
import { selectJogMode } from '../modules/rockhopper/selectors';

export default connect( 
(state, ownProps) => {
  return { 
    jogMode: selectJogMode(state),
  }
}, 
(dispatch, ownProps) => ({ 
  onSetJogMode: (mode) => {
    dispatch( actions.updateHelpers.jogMode(mode) );
    dispatch( actions.put.saveClientConfig({ data: [ 'jogMode', mode ] }) );
  },
}))(JogModeSelect);
