import { connect } from 'react-redux';
import pentaActions from '../modules/pentaui/actions';
import rockhopperActions from '../modules/rockhopper/actions';
import JogButtons from '../components/JogButtons';
import { selectAxisSelectJogAxis, selectAxisSelectJogDigitIndex } from '../modules/pentaui/selectors';
import { selectDisplayUnits, selectJogMode, selectTrajCoordinates, selectUnitsCode } from '../modules/rockhopper/selectors';


export default connect( 
(state, ownProps) => {
  return { 
    axisSelectJogAxis: selectAxisSelectJogAxis(state) ? selectAxisSelectJogAxis(state) : 'X',
    axisSelectJogDigitIndex: selectAxisSelectJogDigitIndex(state) ?  selectAxisSelectJogDigitIndex(state) : 0,
    displayUnits: selectDisplayUnits(state),
    jogMode: selectJogMode(state),
    trajCoords: selectTrajCoordinates(state) ? selectTrajCoordinates(state) : 'XYZAB',
    unitsCode: selectUnitsCode(state),
  }
}, 
(dispatch, ownProps) => ({
  onJogIncrement: (axis, speed, increment, jogMode) => {
    dispatch(rockhopperActions.putHelpers.jogIncrement({ axis, speed, increment, jogMode }));
  },
  onSetOpenDigitIndex: (axis) => {
    dispatch(pentaActions.axisSelectJog.setDigitIndex(axis));
  }
}))(JogButtons);
