import React, {useState, useEffect} from "react";

import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";

import AddIcon from "@material-ui/icons/Add";
import RemoveIcon from "@material-ui/icons/Remove";

import { makeStyles } from "@material-ui/styles";
// import useTranslation from "../hooks/useTranslation";

import DropDownSubmittableTextField from "./DropDownSubmittableTextField.js"

import useUnits, { toMachineUnits } from "../hooks/useUnits";
import { JOG_INCREMENT_LIMITS, JOG_SPEED } from "../modules/rockhopper/constants";

// const calcStepSize = (trajCoords, axisSelectJogAxis, axisSelectJogDigitIndex, getDisplayUnitStringFromType, unitsCode ) => {
//   const jointIndex = trajCoords.indexOf(axisSelectJogAxis);
//   const isRotary = jointIndex > 2;
//   const unit = isRotary ? getDisplayUnitStringFromType("angle") : getDisplayUnitStringFromType("length");
//   const incrementBase = isRotary ? 0.001 : unit === "mm" || (unit === "pu" && unitsCode === "G21") ? 0.001 : 0.0001;
//   return incrementBase * Math.pow(10, axisSelectJogDigitIndex - 1);
// }

const stepSizeOptions = (unit) => {
  switch(unit) {
    case 'deg':
      return [0.001, 0.01, 0.1, 1, 10]
    case 'mm':
      return [0.001, 0.01, 0.1, 1]
    case 'in':
      return [0.0001, 0.001, 0.01, 0.1]
    default:
      return []
  }
}

const useStyles = makeStyles((theme) => ({
  dropDown: {
    color: theme.palette.primary.contrastText,
  },
  extras: {
    display: "flex",
    marginBottom: theme.spacing(1)
  },
  homeAllButton: {
    marginLeft: theme.spacing(1),
  },
  jogButton: {
    margin: theme.spacing(1),
  },
  jogButtonBox: {
    display: "flex",
    justifyContent: "center",
    // margin: theme.spacing(1),
  },
  label: {
    ...theme.typography.statusItem,
    fontSize: 14
  },
  offsetItemContainer: {
    backgroundColor: theme.palette.primary.main,
    borderColor: theme.palette.primary.main,
    // borderRadius: 5,
    borderStyle: "solid",
    borderWidth: 2,
    height: 38,
    width: 100,
    marginTop: theme.spacing(1),
  },
  overrideTextFieldInputInput: {
    color: theme.palette.primary.contrastText,
    backgroundColor: theme.palette.primary.main,
    ...theme.typography.dataText,
  },
  root: {
    height: "100%",
    backgroundColor: theme.palette.primary.main,
    width: "100%",
  },
  unit: {
    alignItems: "center",
    backgroundColor: theme.palette.secondary.main,
    color: "white",
    display: "flex",
    justifyContent: "center",
    height: 42,
    width: 36,
    marginTop: theme.spacing(1)
  }
}));

const JogButtons = (props) => {
  const {
    axisSelectJogAxis,
    axisSelectJogDigitIndex,
    // displayUnits,
    jogMode,
    trajCoords,
    unitsCode,

    onJogIncrement,
    onSetOpenDigitIndex,
  } = props;

  // const { t } = useTranslation();
  const { getDisplayUnitStringFromType } = useUnits();

  useEffect(() => {
    // const step = calcStepSize(trajCoords, axisSelectJogAxis, axisSelectJogDigitIndex, getDisplayUnitStringFromType, unitsCode);
    setStepSize(null)
  }, [axisSelectJogAxis, axisSelectJogDigitIndex, unitsCode]);

  const classes = useStyles();
  const [ stepSize, setStepSize ] = useState(null);

  const jointIndex = trajCoords.indexOf(axisSelectJogAxis);
  const isRotary = jointIndex > 2;
  const unit = isRotary ? getDisplayUnitStringFromType("angle") : getDisplayUnitStringFromType("length");
  const incrementBase = isRotary ? 0.001 : unit === "mm" || (unit === "pu" && unitsCode === "G21") ? 0.001 : 0.0001;
  const axisSelectIncrement = incrementBase * Math.pow(10, axisSelectJogDigitIndex - 1);
  const increment = stepSize ? stepSize : axisSelectIncrement;
  const final = stepSize ? stepSize : increment;

  const handleSelectStepSize = ({index, value}) => {
    setStepSize(value);
    //The axisSelectJogDigitIndex starts at 1 for the position of the smallest joggable digit
    onSetOpenDigitIndex(index + 1);
  };

  const handleSetCustomStepSize = (value) => {
    const floatVal = parseFloat(value);
    const limits = isRotary ? JOG_INCREMENT_LIMITS.ANGULAR : JOG_INCREMENT_LIMITS.LINEAR;
    if( !isNaN(floatVal) && !(floatVal < limits.MIN || floatVal > limits.MAX)){
      const optionsIndex = stepSizeOptions(unit).indexOf(floatVal);
      if(optionsIndex !== -1){
        // console.log('yah')
        //If the value is of form (1 * magnitude), then we can set axisSelectJogDigitIndex to a value other than -1
        //Add 1 because the axisSelectJogDigitIndex starts at 1 for the position of the smallest joggable digit
        onSetOpenDigitIndex(optionsIndex + 1);
      }
      else{
        // console.log('nah')
        // onSetOpenDigitIndex(-1);
      }
      setStepSize(floatVal);
    }
  };

  const handleJog = (direction) => {
    const incMachineUnits = toMachineUnits({value: increment, unit: unit}).value;
    onJogIncrement(jointIndex, direction * JOG_SPEED.LINEAR, incMachineUnits, jogMode);
  };

  return <Box className={classes.jogButtonBox}>
    <Button
      // disableElevation
      size="large"
      color="primary"
      variant="contained"
      className={classes.jogButton}
      onClick={() => handleJog(-1)}
    >
      <RemoveIcon/>
    </Button>
    <DropDownSubmittableTextField

      value={final}
      menuItems={stepSizeOptions(unit)}
      onChange={handleSetCustomStepSize}
      onSelect={handleSelectStepSize}
      onSubmit={(event) => handleSetCustomStepSize(event.target.value)}
      buttonProps={{
        classes: {
          root: classes.dropDown
        }
      }}
      containerProps={{
        className: classes.offsetItemContainer
      }}
      textFieldProps={{
        InputProps: {
          disableUnderline: true,
          classes: {
            input: classes.overrideTextFieldInputInput,
            root: classes.root,
          }
        }
      }}
    />
    <span className={classes.unit}>{unit}</span>
    <Button
      // disableElevation
      size="large"
      color="primary"
      variant="contained"
      className={classes.jogButton}
      onClick={() => handleJog(1)}
    >
      <AddIcon/>
    </Button>
  </Box>
};
export default JogButtons;
